import { createMuiTheme } from '@material-ui/core/styles';
import styled, { CreateStyled } from '@emotion/styled';
import { Theme as MuiTheme } from '@material-ui/core/styles';
import { PaletteOptions, Palette } from '@material-ui/core/styles/createPalette';
import { useTheme as useEmotionTheme } from 'emotion-theming';
import themeConfig from '../../config/theme.yml';

interface IThemePalette extends Palette {
    linearGradient: string;
}
export interface Theme extends MuiTheme {
    palette: IThemePalette;
}

export const useTheme = useEmotionTheme;
export const primaryTheme = createMuiTheme({
    palette: {
        ...themeConfig.theme,
    },
}) as Theme;

// ;

export default styled as CreateStyled<Theme>;
